<template>
    <div id="attendance">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Number of Satsang Attendees : {{users.length}}</h4>
            </li>
            
            <li v-for="user in users" v-bind:key="user.id" class="collection-item">
                
                <div class="chip">{{user.programyear}}</div>
                {{user.name}} - {{user.mobile}} - {{user.location}}

                   
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/" class="btn-floating btn-large blue">
                <i class="fa fa-home"></i>
            </router-link>
        </div>
        
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'attendance',
    data () {
        return {
            users: []
            
        }
    },
    created() {
        db.collection('attendance').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                   'id': doc.id,
                   'mobile': doc.data().mobile,
                   'name': doc.data().name,
                   'location': doc.data().location,
                   'programyear': doc.data().programyear     
                }
                this.users.push(data)
            })
        })
    },
    
}    
</script>


