<template>
    <div id="view-master">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}
                </h4>
            </li>
            <li class="collection-item">Mobile: {{mobile}}</li>
            <li class="collection-item">Location: {{location}}</li>
            <li class="collection-item">Program Year: {{programyear}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="saveUser" class="btn green">Submit</button>
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-master', params: {mobile: mobile}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-master',
    data () {
        return {
            mobile: null,
            name: null,
            location: null,
            programyear: null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('master').where('mobile', '==', to.params.mobile).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.mobile = doc.data().mobile
                    vm.name = doc.data().name
                    vm.location = doc.data().location
                    vm.programyear = doc.data().programyear
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('master').where('mobile', '==', this.$route.params.mobile).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.mobile = doc.data().mobile
                    this.name = doc.data().name
                    this.location = doc.data().location
                    this.programyear = doc.data().programyear
                })
            })
        },
        saveUser () {
            db.collection('attendance').add({
                mobile: this.mobile,
                name: this.name,
                location: this.location,
                programyear: this.programyear
            })
            .then(docRef => {
                this.$router.push('/attendance')
            })
            .catch(error => console.log(err))
           }
        }
    }
   
</script>


