<template>
    <div id="dashboard">
            <input type="text" v-model="search" placeholder="Enter Mobile Number"/>
        <ul class="collection with-header">

            <li class="collection-header">
                <h4>Master Data</h4>
            </li>
            
            <li v-for="user in filteredUsers" v-bind:key="user.id" class="collection-item">
                <div class="chip">{{user.programyear}}</div>
                {{user.name}} : {{user.mobile}} : {{user.location}}

                <router-link class="secondary-content" v-bind:to="{name:'view-master', params: {mobile: user.mobile}}">
                    <i class="fa fa-eye"></i>
                </router-link>    
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            users: [],
            search: ''
        }
    },
    created() {
        db.collection('master').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                   'id': doc.id,
                   'mobile': doc.data().mobile,
                   'name': doc.data().name,
                   'location': doc.data().location,
                   'programyear': doc.data().programyear     
                }
                this.users.push(data)
            })
        })
    },
    computed: {
        filteredUsers: function(){
            return this.users.filter((user) => {
                return user.mobile.match(this.search);
            });
        }
    }
}    
</script>



