<template>
    <div id="edit-master">
        <h4>Edit Master</h4>
        <div class="row">
            <form @submit.prevent="updateUser" class="col s12">
                <div class="row">
                    <div class="input field col s12">
                        <input type="text" v-model="mobile" required>
                         
                    </div>
                </div>
                <div class="row">
                    <div class="input field col s12">
                        <input type="text" v-model="name" required>

                    </div>
                </div>
                <div class="row">
                    <div class="input field col s12">
                        <input type="text" v-model="location" required>
                     
                    </div>
                </div>
                <div class="row">
                    <div class="input field col s12">
                        <input type="text" v-model="programyear" required>
                 
                    </div>
                </div>    
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-master',
    data () {
        return {
            mobile: null,
            name: null,
            location: null,
            programyear: null

        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('master').where('mobile', '==', to.params.mobile).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.mobile = doc.data().mobile
                    vm.name = doc.data().name
                    vm.location = doc.data().location
                    vm.programyear = doc.data().programyear
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('master').where('mobile', '==', this.$route.params.mobile).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.mobile = doc.data().mobile
                    this.name = doc.data().name
                    this.location = doc.data().location
                    this.programyear = doc.data().programyear
                })
            })
        },
        updateUser () {
            db.collection('master').where('mobile', '==', this.$route.params.mobile).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        mobile: this.mobile,
                        name: this.name,
                        location: this.location,
                        programyear: this.programyear
                    })
                    .then(() => {
                        this.$router.push({name: 'view-master', params: {mobile: this.mobile}})
                    })
                })
            })
        }
        
    }
}    
</script>


