import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import editMaster from '@/components/editMaster'
import NewMaster from '@/components/NewMaster'
import viewMaster from '@/components/viewMaster'
import Attendance from '@/components/Attendance'
import Login from '@/components/Login'
import firebase from 'firebase'



Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/attendance',
      name: 'attendance',
      component: Attendance,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/new',
      name: 'new-master',
      component: NewMaster,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/edit/:mobile',
      name: 'edit-master',
      component: editMaster,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/:mobile',
      name: 'view-master',
      component: viewMaster,
      meta: {
        requiresAuth: true
      }
    },
  ]

});

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if(!firebase.auth().currentUser){
      next({
        path: '/login',
        query:{
          redirect: to.fullPath
        }
      });
    } else {
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    if(firebase.auth().currentUser){
      next({
        path: '/',
        query:{
          redirect: to.fullPath
        }
      });
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;


